
<template lang="jade">

.box-page(id="boxPage")
  div(id="boxWrap")
    m-box(v-for="box in boxes", :index="box.index", :title="box.title", :key="box.index", :type="box.type", :href="box.href", @click.native="normalToSpecia(box.index, boxes)").box

</template>
<script>

import _ from 'lodash/fp' 
import { mapGetters } from 'vuex'


//TODO: 限制盒子有效区域
export default {
  data () {
    return {
      size: 120,
      all: 0,
      col: -1,
      row: -1,
      allCol: -1,
      allRow: -1,
      boxes: [],
      specias: [
        { index: 5, href: '/land'},
        { index: 6, href: '/lodash/after', title: 'after'},
        { index: 10, href: '/lodash/ary', title: 'ary'},
        { index: 23, href: '/lodash/before', title: 'before'}
      ]
    }
  },
  mounted () {
    this.initWrap()
    
    //窗口变化时重新渲染
    window.addEventListener('resize', _.debounce(200)(this.initWrap))
  },
  methods: {
    initWrap () {
      let size = this.size
      const width = document.documentElement.clientWidth
      const height = document.documentElement.clientHeight
      
      const col = Math.floor(width / size) 
      const row = Math.floor(height / size)
      const allCol = Math.ceil(width / size)
      const allRow = Math.ceil(height / size)
      
      document.getElementById('boxPage').style.width = width + 'px'
      document.getElementById('boxPage').style.height = height + 'px'
      document.getElementById('boxWrap').style.width = allCol * size + 'px'
      document.getElementById('boxWrap').style.height = allRow * size + 'px'
      
      this.col = col 
      this.row = row
      this.allCol = allCol
      this.allRow = allRow
      this.all = allCol * allRow

      this.checkSpeciaIndex(this.col, this.allCol, this.specias)
      this.createNormal(this.all)
      this.createDisabled(this.col, this.row, this.allCol, this.allRow, this.boxes)
      this.createSpecia(this.specias, this.boxes)
    },

    /**
      创建一定长度的盒子数
      @param length: 指定的盒子数
      @return 生成length数量的盒子数
     */
    createNormal (length) {
      return this.boxes  = _.times(function(key){
        return { index: key, type: 'normal'}
      })(length)
    },

    /**
        创建特殊盒子
        @param specias: 特殊盒子的信息
        @param boxes: 待处理的盒子数组
        @return 将盒子数组中符合条件的普通盒子变为特殊盒子
     */
    createSpecia (specias, boxes) {
      specias.forEach(specia => { 
        let index = specia.index 
        boxes[index].type = 'specia'
        boxes[index].href = specia.href
        boxes[index].title = specia.title
      })
      
      this.boxes = boxes
    },

    /**
      创建不可用的块
      @param col: 可用的列数
      @param row: 可用的行数
      @param allCol: 最大列数
      @param allRow: 最大行数
      @param boxes: 待处理的盒子数组
      @return 将边界超出的盒子设为不可用
     */
     createDisabled (col, row, allCol, allRow, boxes) {

       // 如果可用列数、行数小于边界, 则创建不可用块
       if (col < allCol || row < allRow) {
         boxes.forEach(box => {
            if ((box.index % allCol == col) || (box.index > allCol * row - 1)) {
              boxes[box.index].type = 'disabled'
            }
         })
         return this.boxes = boxes
       }

     },

    /**
        修正特殊块的索引
     */
    checkSpeciaIndex (col, allCol, specias) {

      if (col < allCol) {
        return specias.map(specia => {
          let index = specia.index
          specia.index = index + Math.floor(index / col)
          return specia
        })
      } else return specias
    }, 

    /**
      将指定的普通盒子变为特殊盒子
      @param index: 待转换的盒子真实索引
      @param boxes: 作为来源的盒子数组
      @return 将符合条件的盒子的type设为specia
     */
    normalToSpecia (index, boxes) {
      if (boxes[index].type === 'disabled') {
        return;
      } else {
        boxes[index].type = 'specia'
        return this.boxes = boxes
      }
    }
  }

  //TODO create gooey
  //TODO add color box

}
  
</script>
<style lang="stylus" scoped>
@import '../styles/box-page.styl'

.box-page 
  width 100%
#boxWrap
  font-size 0px
  height 100%
  display flex 
  flex-flow row wrap
.box 
  font-size 16px
</style>


