
<template lang="jade">

.box(:class="type", @click="clickBox", :style="background")
  div.content
    span {{index}}
    icon(name="list").icon
  p.tag {{title}} 

</template>
<style lang="stylus" scoped>
@import '../styles/main.styl'

*
  box-sizing border-box
.normal 
  cursor url(../assets/cursor/default.ico), auto
.specia 
  cursor url(../assets/cursor/link.ico), auto
.disabled 
  cursor url(../assets/cursor/disabled.ico), auto
.box 
  margin 0
  padding 0 
  position relative
  box-sizing border-box
  width box-size
  height @width
  background transparent
  transition all 1.2s ease
  p.tag
    display none
    margin 0 auto
    padding 0.8rem 1rem
    font-size 1.2rem
    font-family 'Abel', sans-serif
    color rgba(233, 233, 233, 1)
.normal, .disabled
  > *
    display none
  &:hover
    background #191919
    transition background 0s linear
.specia
  // background #35A8C3
  border-radius 5%
  &:hover .content 
    opacity 1
  .content 
    width 100%
    padding 0.4rem
    padding-top 0.2rem
    display block
    opacity 0
    transition all .5s linear
    span
      font-size 3rem 
    .icon 
      position absolute
      top 0.4rem 
      right 0.6rem
      transition all .2s linear
      &:hover 
        color #fff
  p.tag 
    display block
    position absolute
    bottom 0

//TODO 为文字添加hover时延迟显示效果


</style>
<script>

export default {
  data () {
    return {
      isLeave: false,
      color: ['#35A8C3', 'rosybrown', 'deepskyblue', 'chocolate', 'teal', 'brown', 'forestgreen', 'lightpink', 'slategray', 'gray']
    }
  },
  computed: {
    background () {
      if (this.type === 'specia') {
        return 'background:' + _.sample(this.color)
      } else {
        return ''
      }
    }
  },
  methods: {
    mouseOut () {
    }, 
    clickBox () {
      if (this.href) {
        return this.$router.push({ path: this.href })
      }
    }
  },
  props: {
    type: {
      type: String,
      default: 'normal'
    },
    bg: {
      type: String,
      default: ''
    },
    href: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: 'links'
    },
    index: {
      type: Number,
      default: null
    }
  }

}
  
</script>


