
<template lang="jade">

.lodash(:key="tag")
  .content(v-html="content")

</template>
<script>

import hljs from 'highlight.js'
import 'highlight.js/styles/hybrid.css'

export default {
  data () {
    return {
      tag: this.$route.params.tag,
      content: ''
    }
  },
  created () {
    this.content = require(`./${this.tag}.md`)
    hljs.configure({
      tabReplace: '  '
    })
  },
  mounted () {
    hljs.initHighlighting()
  }

}
  
</script>
<style lang="stylus" >

.lodash 
  width 100%
  min-height 30rem
  background #EEEEEE
  box-sizing border-box
  overflow hidden
  margin 0 auto
  padding 2rem
  .content
    h1
      margin 1rem 0 0.8rem 0
      font-size 3rem
    h2 
      margin 0.8rem 0 0.4rem 0
    code 
      margin .5rem 0
  
</style>


