<template lang="jade" >

#app 
  keep-alive(:exclude="/.*/")
    router-view 
  
</template>

<script>
export default {
  name: 'app',
  mounted () {
    setTimeout(function(){
      document.getElementById('loaders').style.display = 'none'
    }, 500)
  }
}
</script>

<style lang="stylus">

#app 
  width 100%
  font-size 16px
  font-family 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
a
  text-decoration none
.icon
  width 1em
  height 1em
  vertical-align -0.15em
  fill currentColor
  overflow hidden
</style>



